
div#app {
  width: 100%;
  font-family: 'Titillium Web', sans-serif;
  /*font-family: 'Lato', sans-serif;*/
}
nav {
  text-align: center;
  margin-top: 50px;
  font-size: 150%;
  border-bottom: 1px solid #bbb;
  padding-bottom: 5px;
}
nav a {
  color: #000;
  text-decoration: none;
  padding: 0 30px;
}
nav a span.navDecoration {
  color: #fff;
}
nav a:hover {
  color: #f2511b;
}
nav a:hover span.navDecoration {
  color: #f2511b;
}
nav a.router-link-active {
  color: #f2511b;
}




















































